body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #e4e4e4;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.8) 0%, rgba(59, 72, 98, 0.8) 100%), url(../img/header_bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  padding: 3rem 0 15rem;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding-bottom: 3rem;
  }
}
@media (min-width: 1024px) {
  .header {
    padding-bottom: 10rem;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 20rem;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    width: 25rem;
  }
}
.header__heading {
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 10rem;
    max-width: 50%;
  }
}
.header__texto {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}
.header__imagen {
  display: none;
  max-height: 40rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .header__imagen {
    width: 45%;
    display: block;
    position: absolute;
    right: 0;
    bottom: -5rem;
    border-radius: 2rem 0 0 8rem;
  }
}
@media (min-width: 1024px) {
  .header__imagen {
    width: 40%;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navegacion {
    margin-top: 0;
    flex-direction: row;
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .navegacion {
    gap: 4rem;
  }
}
.navegacion__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 2rem;
}
.navegacion__link:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin-bottom: 0;
  }
}

.propiedades {
  margin: 5rem auto;
}
.propiedades__heading {
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .propiedades__heading {
    text-align: left;
  }
}
.propiedades__heading span {
  font-weight: 700;
}
.propiedades__heading::after {
  content: "";
  display: none;
  width: 15rem;
  margin-top: 1rem;
  height: 1rem;
  background-image: linear-gradient(to left, #3b4862 0%, #3b4862 50%, #000 50%, #000 100%);
}
@media (min-width: 768px) {
  .propiedades__heading::after {
    display: block;
  }
}
@media (min-width: 1024px) {
  .propiedades__heading::after {
    width: 25rem;
  }
}
.propiedades__grid {
  display: grid;
  grid-template-columns: repeat(6, 100%);
  gap: 3rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
}
.propiedades__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 480px) {
  .propiedades__grid {
    grid-template-columns: repeat(6, 40rem);
  }
}
@media (min-width: 768px) {
  .propiedades__grid {
    overflow: unset;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 4rem;
  }
}
@media (min-width: 1024px) {
  .propiedades__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.propiedad {
  background-color: #FFFFFF;
  box-shadow: 5px 5px 2px rgba(0, 0, 0, 0.3);
}
.propiedad:nth-child(1) .propiedad__imagen {
  background-image: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%), url("../img/propiedad_1.jpg");
  /*Hacemos el background transparente automatico, y el de la imagen lo hacemos 150 para que 
  cuando hagamos el scroll no se vea el repeat de la imagen*/
  background-size: auto 180%;
}
.propiedad:nth-child(2) .propiedad__imagen {
  background-image: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%), url("../img/propiedad_2.jpg");
  /*Hacemos el background transparente automatico, y el de la imagen lo hacemos 150 para que 
  cuando hagamos el scroll no se vea el repeat de la imagen*/
  background-size: auto 180%;
}
.propiedad:nth-child(3) .propiedad__imagen {
  background-image: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%), url("../img/propiedad_3.jpg");
  /*Hacemos el background transparente automatico, y el de la imagen lo hacemos 150 para que 
  cuando hagamos el scroll no se vea el repeat de la imagen*/
  background-size: auto 180%;
}
.propiedad:nth-child(4) .propiedad__imagen {
  background-image: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%), url("../img/propiedad_4.jpg");
  /*Hacemos el background transparente automatico, y el de la imagen lo hacemos 150 para que 
  cuando hagamos el scroll no se vea el repeat de la imagen*/
  background-size: auto 180%;
}
.propiedad:nth-child(5) .propiedad__imagen {
  background-image: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%), url("../img/propiedad_5.jpg");
  /*Hacemos el background transparente automatico, y el de la imagen lo hacemos 150 para que 
  cuando hagamos el scroll no se vea el repeat de la imagen*/
  background-size: auto 180%;
}
.propiedad:nth-child(6) .propiedad__imagen {
  background-image: linear-gradient(to top, black 0%, rgba(0, 0, 0, 0) 100%), url("../img/propiedad_6.jpg");
  /*Hacemos el background transparente automatico, y el de la imagen lo hacemos 150 para que 
  cuando hagamos el scroll no se vea el repeat de la imagen*/
  background-size: auto 180%;
}
.propiedad__imagen {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 25rem;
  background-size: cover;
  background-position: center center;
}
.propiedad__pie-imagen {
  margin: 0 0 1rem 0;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
}
.propiedad__contenido {
  padding: 0 2rem;
}
.propiedad__precio {
  font-weight: 700;
  font-size: 2.5rem;
  margin: 0;
}
.propiedad__ver {
  background-color: #3b4862;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .propiedad__ver {
    width: auto;
  }
}
.propiedad__ver:hover {
  background-color: #283142;
}

.destacada {
  margin: 5rem auto;
}
.destacada__grid {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
@media (min-width: 768px) {
  .destacada__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.destacada__imagen, .destacada__imagen img, .destacada__contenido {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.destacada__imagen {
  height: 30rem;
  background-image: url(../img/destacada.jpg);
  background-size: cover;
  background-position: center right;
}
@media (min-width: 768px) {
  .destacada__imagen {
    height: 100%;
  }
}
.destacada__heading {
  text-transform: uppercase;
  text-align: center;
  margin: 2rem auto;
}
.destacada__contenido {
  background-color: #3b4862;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .destacada__contenido {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 2rem;
    margin-left: -1px;
  }
}
.destacada__texto, .destacada__precio {
  color: #FFFFFF;
}
.destacada__ver {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #3b4862;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 80%;
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .destacada__ver {
    width: auto;
  }
}
.destacada__ver:hover {
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .destacada__ver {
    width: 100%;
  }
}
.destacada__precio {
  font-weight: 700;
  font-size: 2.5rem;
  margin: 0;
}

.entrada {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .entrada {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .entrada {
    grid-template-columns: 40% 55%;
  }
}

.testimoniales {
  margin: 5rem auto;
  margin-top: 0;
}
.testimoniales__heading {
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales__heading {
    text-align: left;
  }
}
.testimoniales__heading span {
  font-weight: 700;
}
.testimoniales__heading::after {
  content: "";
  display: none;
  width: 15rem;
  margin-top: 1rem;
  height: 1rem;
  background-image: linear-gradient(to left, #3b4862 0%, #3b4862 50%, #000 50%, #000 100%);
}
@media (min-width: 768px) {
  .testimoniales__heading::after {
    display: block;
  }
}
@media (min-width: 1024px) {
  .testimoniales__heading::after {
    width: 25rem;
  }
}
.testimoniales__heading span {
  display: block;
}

.testimonial__imagen {
  width: 60%;
  margin: 0 auto -10rem auto;
  max-width: 30rem;
}
.testimonial__contenido {
  background-color: #FFFFFF;
