.footer {
  background-color: #3b4862;
}
.footer__logo {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 50%;
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .footer__logo {
    width: 100%;
    grid-column: 1/2;
  }
}
.footer__grid {
  padding: 5rem 0;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 30% 75%;
