.footer__copyright {
  margin: 0;
  padding: 0.5rem;
  text-align: center;
  background-color: #283142;
  color: #FFFFFF;
}

.nosotros {
  margin-top: 5rem;
}
.nosotros__heading, .nosotros__contenido {
